body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: url("../src/assets/images/sleepbg.jpg") no-repeat center center
    fixed;
  background-size: cover;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.container {
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  margin-top: 50px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center; /* Centraliza o conte√∫do horizontalmente */
}

h1 {
  color: #ffd700;
  font-size: 2rem;
  margin: 10px 0;
}

label {
  display: block;
  margin: 10px 0;
  font-size: 1.2em;
  margin-bottom: 20px;
}

input[type="time"] {
  padding: 10px;
  margin-top: 5px;
  border-radius: 5px;
  border: none;
  background-color: #333;
  color: #f0f0f0;
  font-size: 1em;
}

button {
  padding: 10px 20px;
  margin-top: 55px;
  margin-bottom: 40px;
  background-color: #a6b9cf;
  color: #333;
  border: none;
  border-radius: 5px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #ffa500;
}

p {
  max-width: 600px;
  margin: 20px auto;
  font-size: 1em;
  line-height: 1.6;
  color: #dcdcdc;
}

h1 {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #ffffff;
  font-size: 2.5em;
  margin-bottom: 40px;
}

.icon svg {
  width: 32px;
  height: 32px;
}

.footer {
  margin-top: 90px;
  font-size: 0.9em;
  color: #dcdcdc;
  text-align: center;
  opacity: 0.7;
}
.footer a {
  color: #ffffff;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
.language-switcher {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  position: fixed;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;

  position: absolute;
  top: 10px;
}
.label-margin {
  margin-right: 50px;
}
.input-margin {
  margin-left: 10px;
}
